  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  display: grid;
  grid-template-columns: minmax(min-content, 226px) minmax(min-content, 1fr);
  gap: 22px;
  align-items: center;
  padding-bottom: 36px;
  padding-top: 25px;
  margin: 0;
  margin-left: 20px;
}
.additional-product__title::after {
  display: inline-block;
  content: "";
  height: 2px;
  background-color: #38b738;
}
