  padding-top: 13px;
  padding-bottom: 19px;
  width: 100%;
}
@media (min-width: 768px) {
  .additional-product__item {
    border-bottom: none;
  }
}
.additional-product__container-item {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: max-content max-content max-content;
  box-sizing: border-box;
}
.additional-product__name {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  margin: 0;
}
.additional-product__number {
  margin: 0;
  box-sizing: border-box;
  padding-top: 13px;
  padding-bottom: 15px;
  grid-row: 2 / 3;
}
.additional-product__price {
  grid-row: 2 / 3;
  justify-self: end;
}
.additional-product__button {
  grid-column: 2 span;
  grid-row: 3 / 4;
}
@media (min-width: 768px) {
  .additional-product__title {
    font-size: 36px;
    line-height: 36px;
    grid-template-columns: minmax(auto, 20px) minmax(min-content, max-content) minmax(min-content, auto);
    gap: 36px;
    margin-left: 0;
  }
  .additional-product__title::after {
    transform: translateY(0);
  }
  .additional-product__title::before {
    display: block;
    content: "";
    height: 2px;
    background-color: #68b738;
    width: 20px;
  }
  .additional-product__container-item {
    grid-template-columns: auto max-content max-content;
  }
  .additional-product__item {
    border-bottom: 1px solid #d9d9d9;
  }
  .additional-product__number {
    grid-row: unset;
  }
  .additional-product__price {
    grid-row: unset;
    padding-left: 55px;
  }
  .additional-product__button {
    width: 100%;
    grid-column: 3 span;
  }
}
@media (min-width: 1440px) {
