    display: flex;
    margin-bottom: 86px;
  }
  .additional-product__name {
    flex-grow: 1;
  }
  .additional-product__list {
    flex-grow: 1;
  }
  .additional-product__container-item {
    margin: 0;
    display: flex;
    align-items: center;
  }
  .additional-product__number {
    width: 26%;
  }
  .additional-product__price {
    text-align: start;
    padding: 0;
    width: 10%;
  }
  .additional-product__button {
    width: 27%;
